@keyframes spinner {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  .loader {
    width: 50px;
    height: 50px;
    border: 10px solid #f3f3f3; 
    border-top: 10px solid palevioletred; 
    border-radius: 50%;
    animation: spinner 1.5s linear infinite;
  }
  
  .loader-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    height:100vh

  }